.cybersecurity {

  &__inner {
    padding: var(--section-padding);
    border: var(--border-main);
    background-color: var(--color-aqua);
  }

  &__title {
    margin-bottom: 17px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 9px;
  }

  &__item {
    --cyber-li-padding: 13px;
    padding: var(--cyber-li-padding);
    padding-top: 105px;
    border: var(--border-main);
    border-radius: 15px;
    background-color: var(--color-light);
    background-repeat: no-repeat;
    background-position: left var(--cyber-li-padding) top var(--cyber-li-padding);

    &:nth-child(1) {
      background-image: url(../img/cyber1.png);
    }
    &:nth-child(2) {
      background-image: url(../img/cyber2.png);
    }
    &:nth-child(3) {
      background-image: url(../img/cyber3.png);
    }

    &-desc {
      font-weight: 500;
    }
  }
}

@media (min-width: 993px) {
  .cybersecurity {

    &__list {
      display: flex;
      flex-direction: row;
      gap: 19px;
    }

    &__item {
      --cyber-li-padding: 18px;
      flex-basis: 33%;
      padding-top: 139px;
      border-radius: 20px;
      background-size: 93px auto;

      &-desc {
        letter-spacing: 0.075em;
      }

    }

  }
}

@media (min-width: 1200px) {
  .cybersecurity {

    &__item {
      background-size: 103px auto;
    }
  }
}

