.gift {

  &__inner {
    border: var(--border-main);
  }

  &__top {
    padding: max(3.3333333333vw, 27px) max(3.3333333333vw, 13px) max(3.3333333333vw, 29px);
    border-bottom: var(--border-main);
    background-color: var(--color-aqua);
  }

  &__bottom {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 248px;
    padding-bottom: 33px;
    // margin: auto;
    & img {
      height: 75%;
      // object-fit: contain;
      // object-position: 50% 50%;
      // width: 77%;
      // margin-top: -11%;
    }
  }
}

@media (min-width: 993px) {
  .gift {


    &__inner {
      display: flex;

      & > * {
        flex-basis: 50%;
      }
    }

    &__top {
      flex-basis: calc(50% + 1px);
      padding: max(3.3333333333vw, 27px) max(3.3333333333vw, 13px) calc(max(3.3333333333vw, 29px) - 2px);
      border-bottom: none;
      border-right: var(--border-main);
    }

    &__bottom {
      & img {
        height: 79%;
        margin-left: 2%;
        margin-bottom: 2%;
      }
    }
  }
}
