.section-margin {
  margin-bottom: 30px;
}

.main-title {
  margin: 0;
  margin-top: 3px;
  line-height: 0.9;
  font-family: "Humane";
  font-size: max(7.638888888888889vw, 58rem);
  text-transform: uppercase;
  letter-spacing: 0.004em;
  @media (min-width: 1440px) {
    letter-spacing: 0.001em;
  }
}

.desc-basic {
  margin: 0;
  margin-top: 9px;
  line-height: 1.5;
  letter-spacing: 0.033em;
  font-size: calc(16rem + 0.4166666666666667vw);
  font-weight: 600;
  @media (max-width: 550px) {
    font-size: 16rem;
  }
}

.text-medium {
  // font-size: 18rem;
  font-size: calc(18rem - (5.328px - 0.37vw));
  line-height: 150%;
  font-weight: 500;
  letter-spacing: 0.07em;
  @media (max-width: 360px) {
    font-size: 14rem;
    font-weight: 400;

    &--medium {
      font-weight: 500;

    }
  }
}

.section-bottom-text {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  border: var(--border-main);
  border-bottom: none;
  padding: 8px 9px;
  text-transform: uppercase;
  white-space: nowrap;
  letter-spacing: 0.3px;
  font-size: 10rem;
  font-weight: 700;
  line-height: 130%;
  background-color: var(--color-aqua);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.icon {
  display: inline-block;
  padding: 6px 10px;
  border: var(--border-main);
  border-radius: 1.4em;
  font-size: 14px;
  line-height: 130%;
  font-weight: 500;
  white-space: nowrap;
  box-shadow: 3px 3px 0 var(--color-mine-shaft);
  // &:not(:last-child) {
  //   margin-right: 5px;
  // }
  @media (min-width: 993px) {
    &-icon {
      padding: 6px 15px;
      font-size: 16px;
    }
  }
}

.form {
  display: grid;
  grid-template-columns: auto auto;
  gap: 10px;
  padding: 18px;
  border: var(--border-main);
  border-radius: 15px;
  background-color: var(--color-light);
  @media (max-width: 992px) {

  }
  @media (max-width: 550px) {
    padding: 13px;
  }
  @media (max-width: 360px) {
    padding: 13px;
  }

  &__field {
    grid-column: span 2;
    min-width: 0;
    height: 45px;
    padding: 12px 10px;
    border: var(--border-main);
    border-radius: 5px;
    @include hover {
      background-color: var(--hover)
    };
    &.just-validate-error-field {
      border-color: var(--error-stroke);
    }
    @media (max-width: 992px) {
      height: 51px;
    }
    &::placeholder {
      color: #909090;
      // color: red;
      font-weight: 500;
    }
    &-wrap {
      position: relative;
      border: none;
      & select {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background-color: transparent;
      }
    }

    &.inputForSelect {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding-left: 70px;
    }

    &--half {
      grid-column: span 1;
      @media (max-width: 992px) {
        grid-column: span 2;
      }
    }

    &.time {
      &::-webkit-calendar-picker-indicator {
        filter: invert(0.5) sepia(1) saturate(5) hue-rotate(175deg);
        // border-color: #888 transparent transparent transparent;
        // border-style: solid;
        // border-width: 5px 4px 0 4px;
        // left: 50%;
        // margin-left: -4px;
        // margin-top: -2px;
        // top: 50%;
        // width: 0;
      }
    }
  }

  &__submit-btn {
    font-weight: 700;
    background-color: var(--color-amber);
    @include hover {
      background-color: #FFD400;
    };
  }

  &-message-wrong {
    display: none;
    grid-column: span 2;
    padding: 15px;
    font-size: 14px;
    line-height: 150%;
    color: var(--error-stroke);
    background-color: var(--error-fill);
    border: 1px solid var(--error-stroke);

    &.show {
      display: block;
    }
    @media (min-width: 650px) {
      padding: 20px;
    }
  }
}

// select start

.select2 {
  width: 50px !important;
}

.select2-container {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.selection {
  display: flex;
  align-items: center;
  height: 28px;
}

.select2-container .select2-selection--single {
  height: auto;
}

.select2-container--default .select2-selection--single {
  border: none;
  background-color: transparent;
}

.select2-container .select2-selection--single .select2-selection__rendered {
  padding-left: 0;
  padding-right: 20px;
}

.select2-dropdown {
  background-color: var(--color-light);
  border: 2px solid var(--color-amber);
  border-radius: 4px;
  box-sizing: border-box;
  display: block;
  position: absolute;
  left: -100000px;
  width: 100%;
  z-index: 1051;
}

.select2-container--open .select2-dropdown--above {
  border-bottom: 2px solid var(--color-amber);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px
}

.select2-container--open .select2-dropdown--below {
  border-top: 2px solid var(--color-amber);
  border-top-left-radius: 4px;
  border-top-right-radius: 4px
}

.select2-container--open .select2-dropdown {
  left: -6px;
}

.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
  // background-color: #5897fb;
  background-color: var(--color-amber);
  color: white
}
.has-countryflag {
  display: block;
  width: 31px;
  height: 21px;
  background: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: var(--color-light);
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
  top: 0;
  right: 0;
  height: 100%;
}

.select2-container--default .select2-selection--single .select2-selection__arrow b {
  margin: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
  margin-top: -1px;
}


// just-validate start

.just-validate-error-label {
  display: none;
}

.just-validate-error-field {
  background-color: var(--error-fill);
  color: var(--error-stroke);

    &::placeholder {
      color: var(--error-stroke);
    }
  }
