.call {
  // margin-bottom: 300px;

  &__container {
    display: flex;
    @media (max-width: 992px) {
        // flex-direction: column;
        display: block;
    }
    & > * {
      width: 50%;
      border: 2px solid var(--color-mine-shaft);
      @media (max-width: 992px) {
        width: auto;
      }
    }
  }

  &__left, &__right {
    border-top-width: 2px;
  }

  &__left {
    // padding: 48px;
    padding: max(3.333333333333333vw, 27px) max(3.333333333333333vw, 15px);
    // padding-top: ;
    // padding: ;
    border-right-width: 1px;
    background-color: var(--color-aqua);
    @media (max-width: 992px) {
      border-top-width: 0;
      border-right-width: 2px;
    }
  }
  &__right {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 294px;
    // padding: 0 35px;
    border-left-width: 1px;
    @media (max-width: 992px) {
      // padding: 10% 35px;
      // padding-bottom: max(15%, 62px);
      border-top-width: 0;
      border-left-width: 2px;
    }
  }

  &__img {
    // max-width: 340px;
    // min-width: 254px;
    width: 92%;
    @media (max-width: 992px) {
      // height: max-content;
      width: 88%;
      margin-bottom: -9px;
      padding: 6% 0;
    }
  }


  &__title {
    @media (max-width: 362px) {
      margin-right: -2px;
    }
  }

  &__desc {
    margin-bottom: 27px;
    @media (max-width: 992px) {
      margin-bottom: 20px;

    }
  }
}
