.choose {

  &__inner {
    border: var(--border-main);
    background-color: var(--color-aqua);
  }

  &__top {
    border-bottom: var(--border-main);
    padding: max(3.3333333333vw, 27px) max(3.3333333333vw, 13px) max(3.3333333333vw, 25px);
  }

  &__bottom {
    display: flex;
    background-color: var(--color-light);

    & img {
      display: block;
      width: 100%;
      max-width: 516px;
      margin: auto;
    }
  }

  & blockquote {
    margin: 0;
    padding-left: 19px;
    margin-bottom: clamp(13px, 2vw, 20px);
    border-left: 4px solid var(--color-mine-shaft);
  }

  &__desc {

    // &::before {
    //   content: open-quote;
    // }
    // margin-top: 0;

    &-wide {
      margin-bottom: clamp(21px, 4vw, 40px);
      font-weight: 600;
      font-style: normal;
    }
  }

  &__tags {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  &__icon {
    padding: 6px 7px;
    letter-spacing: 0.065em;
    font-weight: 600;
  }
}

@media (min-width: 993px) {
  .choose {

    &__inner {
      display: flex;
      border: var(--border-main);
      background-color: var(--color-aqua);

      & > * {
        flex-basis: 50%;
      }
    }

    &__top {
      border-bottom: none;
      border-right: var(--border-main);
      padding: max(3.3333333333vw, 27px) max(3.3333333333vw, 13px) max(3.3333333333vw, 30px);
    }

    &__bottom {
      display: flex;

      & img {
        margin: auto;
      }
    }

    &__title {
      margin-top: 0;
      margin-bottom: 20px;
    }

    &__desc {
      letter-spacing: 0.045em;

      &-wide {
        margin-bottom: clamp(21px, 4vw, 42px);
        font-weight: 600;
        font-style: normal;
      }
    }

    &__tags {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
    }

    &__icon {
      padding: 6px 12px;
      font-size: 16px;
      letter-spacing: 0.065em;
      font-weight: 500;
    }
  }

}
