.header {
  position: relative;
  z-index: 1;
  // margin-bottom: 1500px;

  &__container {
  }

  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 11px min(3.3vw, 47.52px);
    border: 2px solid var(--color-mine-shaft);
    background-color: var(--color-aqua);
    @media (max-width: 1200px) {
      padding: 11px 2vw;
    }
    @media (max-width: 360px) {
      padding: 7px 4vw;
    }
  }

  &__logo {

    & img {
      @media (max-width: 1200px) {
        width: 180px;
      }
      @media (max-width: 360px) {
        width: 165px;
      }
    }
  }

  &__nav {
    @media (max-width: 890px) {
      position: absolute;
      z-index: 1;
      top: 100%;
      right: 0;
      max-width: 500px;
      width: 100%;
      min-height: 553px;
      padding: 60px 0;
      color: var(--color-light);
      background-color: var(--color-god-gray);
      transform: translateX(120%);
      transition: ease-in-out 0.3s;
      &.menu--active {
        z-index: 1;
        transform: translateX(0);
        transition: ease-in-out 0.3s;
      }
    }
  }

  &__list {
    display: flex;
    @media (max-width: 890px) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {
    @include mr(min(1.805555555555556vw, 26px));
    @media (max-width: 890px) {
      @include mr(0);
      @include mb(30px);
      letter-spacing: 1px;
    }
  }

  &__link, &__phone {
    font-size: 14rem;
    font-weight: 600;
    text-transform: uppercase;
    color: inherit;
    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 2px;
      background-color: var(--color-mine-shaft);
      transform: scaleX(0);
      transform-origin: 0%;

    }
    @include hover {
      &::after {
        transform: scaleX(1);
        // transform-origin: 0%;
        transition: .2s ease-in-out;
      }
    }
    @media (max-width: 1200px) {
      font-size: 13rem;
    }
  }

  &__phone {
    @media (max-width: 1050px) {
      display: none;
    }
  }

  &__burger-wrap {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    aspect-ratio: 1 / 1;
    @media (min-width: 891px) {
      display: none;
    }
    border-left: var(--border-main);
  }

  &__burger {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    // transform: translate(-50%);
    @media (min-width: 891px) {
      display: none;
    }
    --burger-width: 22px;
    --burger-height: 17px;
    --burger-line-height: 2px;
    color: var(--color-mine-shaft);
    &.burger--active {
      z-index: 2;
    }


  }
}
