.courses {

  &__inner {
    padding: var(--section-padding);
    border: var(--border-main);
    background-color: var(--color-aqua);
  }

  &__title {
    margin-bottom: 20px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    padding: 20px 0;
    padding-bottom: 18px;
    border: var(--border-main);
    border-radius: calc(var(--li-padding) + 5px);
    background-color: var(--color-light);
    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &-top {
      padding: 0 var(--li-padding);
      padding-bottom: calc(var(--li-padding) - 3px);
    }

    &-bottom {
      display: flex;
      margin-top: auto;
      padding: 0 var(--li-padding);
      padding-top: var(--li-padding);
      border-top: var(--border-main);
    }

    &-title {
      margin-bottom: 10px;
      font-family: "Humane";
      font-weight: 700;
      font-size: 58px;
      line-height: 90%;
    }

    &-icon {
      display: inline-block;
      padding: 6px 10px;
      border: var(--border-main);
      border-radius: 1.4em;
      font-size: 14px;
      line-height: 130%;
      font-weight: 500;
      white-space: nowrap;
      background-color: var(--color-amber);
      box-shadow: 3px 3px 0 var(--color-mine-shaft);
      &:not(:last-child) {
        margin-right: 5px;
      }
    }
  }
}


@media (min-width: 361px) {
  .courses {

    &__item {

      &-title {
        // font-size: calc(80rem - (29px - 2.013888888888889vw));
        // font-size: calc(80rem - (29.304px - 2.035vw));
        font-size: calc( (100vw - 360rem)/(1440 - 360) * (80 - 58) + 58rem);

      }
    }

  }
}
@media (min-width: 993px) {
  .courses {

    &__list {
      display: flex;
      gap: 20px;
    }

    &__item {
      padding: 17px 0;

      &:not(:last-child) {
        margin-bottom: 0;
      }

      &-title {
        margin-bottom: 0;
        line-height: 100%;
      }

      &-icon {
        padding: 6px 15px;
        font-size: 16px;
      }

    }

  }
}
