.employment {
  // margin-bottom: 200px;


  &__inner {
    border: var(--border-main);
  }

  &__top {
    padding: var(--section-padding);
    padding: max(3.3333333333vw, 27px) max(3.3333333333vw, 13px) max(3.3333333333vw, 30px);
    background-color: var(--color-aqua);
  }

  &__bottom {
    border-top: var(--border-main);
    // background-color: var(--color-aqua);

    & img {
      display: block;
      width: 100%;
    }
  }

  &__list {
    margin-top: 15px;
    margin-left: 10px;
    margin-bottom: clamp(18px, 2.5vw, 27px);
    list-style-type: none;
    line-height: 1.3;


    & li {
      display: inline-flex;
      align-items: center;
      // position: relative;

      &::before {
        // position: relative;
        content: '';
        display: inline-block;
        height: .1875em;
        width: .1875em;
        border-radius: 1ch;
        margin-inline-end: .7em;
        // margin-bottom: .3em;
        vertical-align: middle;
        background-color: var(--color-mine-shaft);
      }
    }

  }
}

@media (min-width: 993px) {
  .employment {

    &__inner {
      display: flex;

      & > * {
        width: 50%;
      }
    }

    &__bottom {
      display: flex;
      margin-top: 0;
      border-top: none;
      border-left: var(--border-main);
      & img {
        max-width: 86.91860465116279%;
        margin: auto;

      }
    }

    &__desc {
      padding-right: 10%;
    }
  }
}
