// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Montserrat", "Open Sans", sans-serif;
  --content-width: 1380px;
  --container-offset: 30px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  @media (max-width: 768px) {
    --container-offset: 15px;
  }
  @media (max-width: 360px) {
    --container-offset: 15px;
  }

  // colors
  --color-light: #fff;
  --color-aqua: #b1e0df;
  --color-mine-shaft: #222;
  --color-god-gray: #111;
  --color-amber: #fabd05;
  --color-gray: #909090;
  --error-fill: #f3e0e0;
  --error-stroke: #AA0E0E;
  --hover: #f4f4f4;

  // other
  --border-main: 2px solid var(--color-mine-shaft);

  --section-padding: max(3.3333333333vw, 27px) max(3.3333333333vw, 13px);

  // --li-padding 1440 - 20px 360 - 15px
  --li-padding: calc(20px - (6.624px - 0.46vw));
  @media (max-width: 360px) {
    --li-padding: 15px;
  }

}
