.footer {
  margin-bottom: 200px;

  &__inner {
    background-color: var(--color-mine-shaft);
    padding: max(3.3333333333vw, 30px) max(3.3333333333vw, 15px);
  }

  &__copyright {
    color: var(--color-gray);
  }

  &__social {
    display: flex;
    gap: 15px;
    margin-bottom: 20px;
  }
}

.social {

  &__item {
    color: var(--color-gray);
  }


  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    color: var(--color-gray);
    border-radius: 100%;
    border: 2px solid currentColor;

    & svg {
      --width-height-svg: 62%;
      width: var(--width-height-svg);
      height: var(--width-height-svg);
      fill: currentColor;
    }
  }
}

@media (min-width: 993px) {
  .footer {

    &__inner {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
    }

    &__copyright {
      max-width: 50%;
      margin-top: 0;
    }

  }

  .social {

    &__item {
      color: var(--color-gray);
    }


    &__link {
      width: 42px;
      height: 42px;
    }
  }
}
