.say {

  &__inner {
    padding: max(3.3333333333vw, 37px) max(3.3333333333vw, 13px);;
    border: var(--border-main);
    background-color: var(--color-aqua);
  }

  &__title {
    margin-bottom: 18px;
  }

  &__list {
    // display: flex;
    // flex-direction: column;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 10px;


    & li {
      padding: 15px 14px 13px 15px;
      border: var(--border-main);
      border-radius: 15px;
      background-color: var(--color-light);

      & blockquote {

        & .wrap {
          display: flex;
          align-items: center;
          gap: 10px;
          margin-bottom: 10px;
          padding-right: 5%;
        }

        & cite {
          font-size: 16px;
          font-weight: 700;
          font-style: normal;

          & span {
            font-weight: 500;
          }

        }

      }
    }
  }

  &__ratio {
    display: flex;
    gap: 1px;
    margin-bottom: 12px;
  }

  &__text.text-medium.text-medium--medium {
    line-height: 143%;
  }
}

@media (min-width: 600px) {
  .say {

    &__title {
      margin-top: 2px;
    }

    &__list {
      grid-template-columns: repeat(auto-fit, minmax(306px, 1fr));
      gap: 20px;

      & li {
        min-width: 300px;
        // width: 413px;
        flex-grow: 1;
        padding: 15px 10px 13px 15px;

      }
    }
    & .wrap {

      & img {
        width: 70px;
      }
    }

      &__text.text-medium.text-medium--medium {
        font-size: 16px;
        line-height: 150%;
      }
  }
}
