  //   border: var(--border-main);
  //   border-bottom: none;
  //   padding: 8px 9px;
  //   text-transform: uppercase;
  //   white-space: nowrap;
  //   letter-spacing: 0.3px;
  //   font-size: 10rem;
  //   font-weight: 700;
  //   line-height: 130%;
  //   background-color: var(--color-aqua);
  //   border-top-left-radius: 10px;
  //   border-top-right-radius: 10px;
  // }

  &__title {

  }

  &__desc {
    margin-bottom: 27px;
    @media (max-width: 992px) {
      margin-bottom: 20px;

    }
  }
}
