.video {

  &__top {
    padding: max(3.333333333333333vw, 27px) max(3.333333333333333vw, 15px);
    padding-bottom: 18px;
    background-color: var(--color-aqua);
    border: 2px solid var(--color-mine-shaft);
    border-bottom-width: 1px;
  }

  &__bottom {
    position: relative;
    border: 2px solid var(--color-mine-shaft);
    border-top-width: 1px;
  }

  &__video {
    display: block;
    aspect-ratio: 338 / 453;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100px;
    height: 100px;
    &::before {
      content: "";
      display: block;
      width: 103%;
      height: 103%;
      background-image: url(../img/button-play.svg);
    }
  }
}

@media (min-width: 993px) {
  .video {

    &__container {
      display: flex;

      & > * {
        width: 50%;
      }
    }

    &__top {
      border: 2px solid var(--color-mine-shaft);
      border-right-width: 1px;
    }

    &__bottom {
      border: 2px solid var(--color-mine-shaft);
      border-left-width: 1px;
    }

    &__video {
      aspect-ratio: 697 / 707;
    }
  }
}
