.learn {

  &__container {

  }

  &__inner {
    padding: max(3.333333333333333vw, 27px) max(3.333333333333333vw, 13px);
    border: var(--border-main);
    background-color: var(--color-aqua);
  }

  &__title {
    // grid-column: span 1;
  }

  &__desc {

  }

  &__list {
    margin: 0;
    margin-top: 20px;
    list-style-position: inside;
    counter-reset: ex 0;
    grid-column: span 2;
  }

  &__title-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 9px;
  }

  &__item {
    position: relative;
    padding: calc(var(--li-padding) - 2px);
    padding-bottom: calc(var(--li-padding) - 2px);
    border: var(--border-main);
    border-radius: var(--li-padding);
    background-color: var(--color-light);
    &:not(:last-child) {
      margin-bottom: 15px;
    }

    &-button {
      position: relative;
      flex-shrink: 0;
      width: 42px;
      height: 42px;
      // right: var(--li-padding);
      border: var(--border-main);
      border-radius: 100%;
      background-color: var(--color-amber);
      margin-left: 12px;
      &::before, &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        width: 20px;
        height: 3px;
        background-color: var(--color-mine-shaft);
      }
      &::before {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
      &::after {
        transform: translate(-50%, -50%) rotate(45deg);
      }
    }

    &-title {
      display: inline-block;
      margin-top: 0;
      counter-increment: ex 1;
      &::before {
        margin-right: 0.2em;
        content: counter(ex, decimal-leading-zero) '.';
        font-size: inherit;
        font-weight: 900;
        letter-spacing: -0.8px;
      }
    }

    &-text {
      // padding-right: calc(60px + var(--li-padding));
      // padding-right: 53px;
      padding-right: 19.63%;
    }
  }
}

@media (min-width: 993px) {
  .learn {
    &__inner {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 5%;
    }

    &__desc {
    margin-top: 0;
    }

    &__title-wrap {
      margin-bottom: 0;
    }

    &__item-text {
      padding-right: 50%;
    }
  }
}
